	module.exports = {"data":{"site":{"siteMetadata":{"title":"Annalisa Valente"}},"contentfulPageSelectorPanel":{"pages":[{"name":"Brave the Shave","title":null,"slug":"/projects/macmillan","description":"Redesigning Brave the Shave microsite"},{"name":"Mindfulness Moments","title":"Mindfulness Moments","slug":"/projects/mindfulness-moments","description":"Microsite for Macmillan Cancer Support"},{"name":"Local Cancer Intelligence","title":"Local cancers intelligence","slug":"/projects/new-page","description":"A new description"},{"name":"Google","title":"Google","slug":"/projects/google/","description":"Interaction designer contractor in the Google Play Console team"},{"name":"The Telegraph","title":"The Telegraph","slug":"/projects/thetelegraph/","description":"Working on the mobile The Telegraph touch points"},{"name":"The Telegraph puzzle ","title":null,"slug":"/projects/the-telegraph-puzzle/","description":"Redesign the experience of the Telegraph puzzle website."},{"name":"Nectar ","title":null,"slug":"/projects/nectar/","description":"  Delivering a new personalised user experience on Nectar web and iOS-Android app."},{"name":"MyMail & Nectar","title":null,"slug":"/projects/mymail-and-nectar/","description":"Launch of MyMail and Nectar proposition to collect Nectar points with the DailyMail."},{"name":"Remembering a name and a face","title":null,"slug":"/projects/face-name/","description":"Concept of an android experience that helps to match faces to names."}]},"contentfulPage":{"slug":"/projects/thetelegraph/","password":null,"panels":[{"__typename":"ContentfulHeaderPanel","text":null},{"__typename":"ContentfulContentPanel","content":{"content":"__My role:__ UX Designer\n\n__Projects:__ Edition app - Live app - My Telegraph \n\n\n\n__Process and way of working__\n\nAt The Telegraph I worked in the product team as a UX designer and my responsibility was to deliver an engaging reading experience to the readers.\n\nWith a fast prototyping and early testing approach we improved the products building essential features that increased the engagement and the consumption of articles improving the online and offline experiences.\n\n__Design sprint__\n\nI was involved in various design sprints to help to shape the future of the products in order to create a superior experience for the users. Working closely to the PO, stakeholders, user researchers and business folks, my duty was to help the team in order to shape the product’s roadmap and prioritise features. \n\n\n__My Telegraph: a new way to get the news__\n\nWhat we did and what we implemented:\n\n- I helped to develop the new personas for the new propositions working closely with the researcher teams and analytics team. We created 5 different personas that the product team used to developed the new proposition. At the end of the design sprint we tested the concept with real users.\n\n\n\n\n\n\n\n","childMarkdownRemark":{"html":"<p><strong>My role:</strong> UX Designer</p>\n<p><strong>Projects:</strong> Edition app - Live app - My Telegraph </p>\n<p><strong>Process and way of working</strong></p>\n<p>At The Telegraph I worked in the product team as a UX designer and my responsibility was to deliver an engaging reading experience to the readers.</p>\n<p>With a fast prototyping and early testing approach we improved the products building essential features that increased the engagement and the consumption of articles improving the online and offline experiences.</p>\n<p><strong>Design sprint</strong></p>\n<p>I was involved in various design sprints to help to shape the future of the products in order to create a superior experience for the users. Working closely to the PO, stakeholders, user researchers and business folks, my duty was to help the team in order to shape the product’s roadmap and prioritise features. </p>\n<p><strong>My Telegraph: a new way to get the news</strong></p>\n<p>What we did and what we implemented:</p>\n<ul>\n<li>I helped to develop the new personas for the new propositions working closely with the researcher teams and analytics team. We created 5 different personas that the product team used to developed the new proposition. At the end of the design sprint we tested the concept with real users.</li>\n</ul>"}}},{"__typename":"ContentfulImagePanel","fullWidth":true,"media":{"description":"","file":{"url":"//images.ctfassets.net/wk0hltq7eofw/5t1FeWY2vmQIaGUoa8QEWA/20604a93ce1f17d843fa7ef03e33bbec/Artboard.jpg"}}},{"__typename":"ContentfulContentPanel","content":{"content":"\n- Following a design sprint we developed My Telegraph section: after collecting feedback from users of the Telegraph we found out that people’d like to personalise the feeds and to save articles. My telegraph is the personal space of the user. It contains: all saved articles, content tailored for the user based on their behaviour and precedent activities. \n- Updated navigation and section pages\n","childMarkdownRemark":{"html":"<ul>\n<li>Following a design sprint we developed My Telegraph section: after collecting feedback from users of the Telegraph we found out that people’d like to personalise the feeds and to save articles. My telegraph is the personal space of the user. It contains: all saved articles, content tailored for the user based on their behaviour and precedent activities. </li>\n<li>Updated navigation and section pages</li>\n</ul>"}}},{"__typename":"ContentfulImagePanel","fullWidth":null,"media":{"description":"","file":{"url":"//images.ctfassets.net/wk0hltq7eofw/4dcZv1rqPuSeMKUyYAUUUQ/832b556ff3b7e250b81e3e0b126ea546/Screen_Shot_2018-10-23_at_17.25.17.png"}}},{"__typename":"ContentfulImagePanel","fullWidth":null,"media":{"description":"","file":{"url":"//images.ctfassets.net/wk0hltq7eofw/v7h6FpVIHYIQUmi8gk4IG/cbb36b791431f86788b90cb8375f3a61/Screen_Shot_2018-10-23_at_17.27.28.png"}}},{"__typename":"ContentfulContentPanel","content":{"content":"At the end of the design sprint we tested the concept with real users.\n","childMarkdownRemark":{"html":"<p>At the end of the design sprint we tested the concept with real users.</p>"}}},{"__typename":"ContentfulImagePanel","fullWidth":true,"media":{"description":"","file":{"url":"//images.ctfassets.net/wk0hltq7eofw/5BwcifK7FS8SMkU6UiUCs6/ceb97a691dd11261e0623d93a34aacb6/teleg.jpg"}}},{"__typename":"ContentfulImagePanel","fullWidth":true,"media":{"description":"","file":{"url":"//images.ctfassets.net/wk0hltq7eofw/1rOCWixSXWG0QUSsys84KG/482ddc562d00d4111e6fd98a4bfad623/telegraph_evening.jpg"}}},{"__typename":"ContentfulContentPanel","content":{"content":"You can find the result of a week of design sprint [here](https://marvelapp.com/216gb48/screen/30386778 \"here\").\n\nThe concept of \"My Telegraph\" that we designed during the sprint was evaluated by the team and is now live and have been adapted in all the digital touchpoints of The Telegraph like live app and website.\n\nRead [more info](https://www.telegraph.co.uk/news/2018/07/06/introducing-telegraph-new-way-get-news \"live\") \"more info\") regarding the project that went live in July 2018.","childMarkdownRemark":{"html":"<p>You can find the result of a week of design sprint <a href=\"https://marvelapp.com/216gb48/screen/30386778\" title=\"here\">here</a>.</p>\n<p>The concept of \"My Telegraph\" that we designed during the sprint was evaluated by the team and is now live and have been adapted in all the digital touchpoints of The Telegraph like live app and website.</p>\n<p>Read <a href=\"https://www.telegraph.co.uk/news/2018/07/06/introducing-telegraph-new-way-get-news\" title=\"live\">more info</a> \"more info\") regarding the project that went live in July 2018.</p>"}}},{"__typename":"ContentfulImagePanel","fullWidth":true,"media":{"description":"","file":{"url":"//images.ctfassets.net/wk0hltq7eofw/3cRm64rKDSgWuo8OegaqaM/29b770582d68a82a5b9dc6bf8f6b097c/telegraph.jpg"}}}]}},"pathContext":{"slug":"/projects/thetelegraph/","p":"35675e68f4b5af7b995d9205ad0fc43842f16450"}}

